.about-slide3 {
    background-color: var(--color-secondary);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

}

.about-slide3-titles-container {
    width: 70%;
    height: 90%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    opacity: 0;
    transform: translateY(200px);
    transition: opacity 0.8s ease-in-out, transform 0.8s ease-in-out;
    position: relative;
}

