        display: flex;
        padding-right: 0px;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;



    }

    .about-slide3-title-container {

        padding: 10px;
        margin: 10px;
        background-color: var(--color-primary);
    }

    .about-slide3-title {
        font-weight: 100;
        font-size: 30px;
        margin-top: 10px;

    }


    .icon-skill {
        width: 70px;

    }

    .html-icon,
    .css-icon,
    .typescript-icon {
        transform: translateY(50px);
    }

    .react-icon {
        transform: translateY(50px);
    }

    .next-icon {
        transform: translateY(50px);
    }

    .redux-icon,
    .figma-icon,
    .github-icon {
        transform: translateY(50px);
    }

}